<script>
import { applicationService } from "@/services";
import { mapState, mapActions } from "vuex";
import { router } from "@/route/router";

export default {
  data() {
    return {
      applications: [],
      authenticating: false
    };
  },
  created() {
    this.getAllApplications();
  },
  methods: {
    getAllApplications() {
      applicationService.getAll().then(data => {
        this.applications = data.data;
      });
    },
    ...mapActions({
      authApplicationByID: "root/authApplicationByID"
    }),
    authApp(id) {
      this.authenticating = true;
      this.authApplicationByID(id)
        .then(x => {
          router.push("home");
        })
        .catch(x => {
          alert("Não foi possível autenticar");
        })
        .finally(x => {
          this.authenticating = false;
        });
    }
  }
};
</script>
<template>
  <div class="page-wrapper">
    <div class="panel">
      <div class="panel-heading">
        <span class="title">Selecione uma aplicação para continuar</span>
      </div>
      <div class="panel-body">
        <div v-if="!applications.length">Carregando aplicações...</div>
        <ul class="listing" v-if="!authenticating">
          <li class="btn-link" v-for="app in applications" :key="app.id">
            <a
              :href="'/#/home?auth_application_id='+app.id"
              @click.prevent="authApp(app.id)"
            >{{app.name}}</a>
          </li>
        </ul>
        <span class="authenticating" v-if="authenticating">Autenticando...</span>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.page-wrapper {
  padding: 15px 30px;
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;

  .panel {
    width: 500px;
    font-family: roboto;

    .panel-heading {
      .title {
        font-weight: 500;
        font-size: 17px;
      }
    }

    .panel-body {
      ul.listing {
        display: flex;
        flex-direction: column;

        & > li {
          padding: 10px 10px;
          font-weight: 500;
          border-bottom: 1px solid #eee;
        }
      }
      .authenticating {
        color: #777;
        font-weight: 500;
      }
    }
  }
}
</style>


